// @flow
import type { Action } from '../types';
import type { Cards } from '../types/cards';

export default (
	state: Cards = [],
	action: Action
): Cards => {
	/**
	 *   
	 * @CREATE_VIEW -    
	 * */
	switch (action.type) {
		case 'CREATE_VIEW': {
			return [...action.payload];
		}
		default:
			return state;
	}
}



